<template>
  <div class="detial">
    <div class="detial-title">
      <span class="detail-icon" @click="backlastpage"></span>
      详情页
    </div>   
    <detialswiper :top-images="topimages"></detialswiper>
    <detialdescri :columns="columns" :titledd="titledd" :lownowprice="lownowprice" :oldprice="oldprice"></detialdescri>
    <detialshop :shopinfo="shopinfo"></detialshop>
    <detialinfo :detailInfo="detialInfo"></detialinfo>
    <detialbottombar @addCart="addToCart"></detialbottombar>
    
  </div>
</template>


<script>
import { getdetail } from "../../network/detial";
import detialswiper from "./maincontent/DetialSwiper";
import { connect } from "tls";
import detialdescri from "./maincontent/Detialdescri"
import detialshop from "./maincontent/Detialshop"
import detialinfo from "./maincontent/Detialinfo"
import detialbottombar from './maincontent/Detialbottombar'

export default {
  name: "Detial",
  components: {
    detialswiper,
    detialdescri,
    detialshop,
    detialinfo,
    detialbottombar
  },
  data() {
    return {
      iid: null,
      flag: "detail",
      topimages: [],
      titledd: '',
      lownowprice: '',
      oldprice: '',
      columns: [],
      shopinfo: {},
      detialInfo: {}
    };
  },

  created() {        
    //存储iid,
    this.iid = this.$route.params.iid;
    console.log(this.iid);
    //请求数据
    getdetail(this.iid).then(res => {
      console.log(res);
      this.topimages = res.result.itemInfo.topImages
      this.titledd = res.result.detailInfo.desc
      this.lownowprice = res.result.itemInfo.lowNowPrice
      this.oldprice = res.result.itemInfo.lowPrice
      this.columns = res.result.columns
      //console.log(this.columns)
      this.shopinfo = res.result.shopInfo
      this.detialInfo = res.result.detailInfo
    });
  },
  destroyed() {
    console.clear()
  },
  methods: {
    backlastpage() {
      this.$router.go(-1);
    },
    addToCart() {
      //获取商品信息
      const product = {}
      product.image = this.topimages[0];
      product.title = this.titledd;
      product.price = this.lownowprice;
      product.iid = this.iid;

      //将商品添加到购物车使用vuex保存该对象
      //this.$store.commit('addCart', product)
      this.$store.dispatch('addCart', product)
    }
  }
};
</script>


<style>
body {
  margin: 0;
  padding: 0;
}
.detial{
  position: relative;
  z-index: 7;
  background-color: white;  
  width: 100%;
} 
.detial-title {
  height: 44px;
  text-align: center;
  font-size: 20px;
  width: 100%;
  background-color: red;
  line-height: 44px;
  font-weight: 600;
  color: white;
  position: fixed;
  z-index: 20;
}
.detail-icon {
  content: "";
  font-family: "icomoon";
  float: left;
  line-height: 44px;
  font-size: 23px;
  color: white;
  margin-left: 15px;
  margin-right: -15px;
}
</style>
