<template>
  <div class="detial-shop">
    <div class="shopinfo-logo">
      <img :src="shopinfo.shopLogo" alt>
      {{shopinfo.name}}
    </div>

    <div class="shopinfo-contant-left">
      <div class="shopinfo-collect">收藏人数:{{shopinfo.cFans}}</div>
      <div class="shopinfo-sell">商品售出:{{shopinfo.cSells}}</div>
      <div class="shopinfo-goods">点赞人数:{{shopinfo.cGoods}}</div>
    </div>

    <div class="shopinfo-contant-right">
      <!-- 这里的三层选择,在vue的机制中页面渲染后请求的数据会发生结构改变,但是我不会修改，参考了csdn添加选择但是会出现DOM丢失的问题 -->
      <!-- 想了个办法在组件里对数据先进行一次处理 =>放弃···-->
      <div class="shopinfo-match" v-if='shopinfo.score'>{{shopinfo.score[0].name}}:{{shopinfo.score[0].score}}</div>
      <div class="shopinfo-price" v-if='shopinfo.score'>{{shopinfo.score[1].name}}:{{shopinfo.score[1].score}}</div>
      <div class="shopinfo-colity" v-if='shopinfo.score'>{{shopinfo.score[2].name}}:{{shopinfo.score[2].score}}</div>
    </div>

    <div class="getintotheshop"><a :href="shopinfo.shopUrl">进入店铺</a></div>
  </div>
</template>
<script>
export default {
  name: "Detialshop",
  props: {
    shopinfo: {
      type: Object
    }
  },
};
</script>


<style>
