  box-sizing: border-box;
  padding-left: 20px;
  padding-top: 20px;
}
.shopinfo-logo img {
  vertical-align: middle;
  height: 60px;
  margin-right: 30px;
}
.shopinfo-contant-left {
  margin: 20px 0px 0px 20px;
  line-height: 1.5;
  border-right: 1px solid gray;
  width: 45%;
  box-sizing: border-box;
  float: left;
}
.shopinfo-collect {
  color: hotpink;
}
.shopinfo-sell {
  color: skyblue;
}
.shopinfo-goods {
  color: green;
}
.shopinfo-contant-right {
  line-height: 1.5;
